export default {
  init (config, { event }) {
    const { breakpoints } = config

    let currentBreak = 0

    for (let i = 0; i < breakpoints.length; i++) {
      if (window.innerWidth > breakpoints[i]) {
        currentBreak = i
      }
    }

    // event.on('pageWidthChanged', () => { console.log('changed to new breakpoint') })

    const sizeCheck = () => {
      let screenBreak
      let screenW = window.innerWidth

      for (let i = 0; i < breakpoints.length; i++) {
        if( screenW > breakpoints[i]){
          screenBreak = i;
          // bp = breakpoints[i]; //this will be the px value defined in the breakpoints array
          continue
        }
      }

      if (screenBreak != currentBreak) {
        currentBreak = screenBreak;
        switch( currentBreak ) {
            case 0 : event.trigger('pageWidthChanged') /* the first breakpoint or larger*/ ; break;
            case 1 : event.trigger('pageWidthChanged') /* second */ ; break;
            case 2 : /* third */; break;
            default : event.trigger('pageWidthChanged') /* stuff that happens below the lowest one */;
          }
      }
    }

    window.addEventListener('resize', sizeCheck)
  }
}
