import Tagify from "@yaireo/tagify";
import "@yaireo/tagify/dist/tagify.polyfills.min.js";

export default {
  init(cfg, ctx) {
    const { event } = ctx;
    const { labels } = window.JSi18n.FE;
    let usedShares = "";
    let tagify;

    var input = document.querySelector("input[name=shareEmailtoken]");
    infoq.initShareTokens = initShareTokens;
    infoq.parseJson = parseJson;

    if (!input) return;

    function initShareTokens(
      csvShares,
      csvUsedShares,
      limitTokens,
      sharesCount
    ) {
      usedShares = csvShares;
      tagify = new Tagify(input, {
        pattern: /^[a-zA-Z0-9_+&*-]+(?:\.[a-zA-Z0-9_+&*-]+)*@(?:[a-zA-Z0-9-]+\.)+[a-zA-Z]{2,9}$/i,
        delimiters: /[\s|,]/gm,
        editTags: 0,
        placeholder: "example@domain.com",
        duplicates: false,
        keepInvalidTags: true,

        maxTags: parseInt(limitTokens),
        templates: {
          wrapper(input, settings) {
            return `<tags class="tagify ${settings.mode ? "tagify--mix" : ""} ${
              input.className
            }" ${settings.readonly ? "readonly" : ""}>
                                <span contenteditable data-placeholder="${input.placeholder ||
                                  "example@domain.com"}" class="tagify__input"></span>
                            </tags>`;
          },
          tag(v, tagData) {
            return `<tag contenteditable='false' spellcheck="false" class='tagify__tag ${
              tagData.class ? tagData.class : ""
            }' ${this.getAttributes(tagData)}>
                                <x title='' class='tagify__tag__removeBtn'></x><div><span class='tagify__tag-text'>${v}</span></div>
                            </tag>`;
          }
        }
      });

      tagify
        .on("add", onAddTag)
        .on("remove", onRemoveTag)
        .on("invalid", onInvalidTag);

      var elementsToRemove = csvShares.split(",").filter(function(n) {
        return csvUsedShares.split(",").indexOf(n) !== -1;
      });

      csvShares = csvShares.split(",").filter(function(el) {
        return elementsToRemove.indexOf(el) < 0;
      });

      csvShares = csvShares.join(",");

      tagify.addTags(csvUsedShares).forEach(tag => {
        tag && tag.classList.add("used");
        tag && tag.setAttribute("readonly", "true");
      });

      tagify.addTags(csvShares);

      document.getElementById("token-added").innerHTML = sharesCount;
      document.getElementById("max-tokens").innerHTML = limitTokens;
      document.getElementById("shareSubmit").disabled = true;

      return tagify;
    }

    function onAddTag(e) {
      if (tagify) {
        var validInputs = document.getElementsByClassName("tagify__tag");
        var clonedObj = [].slice.call(validInputs);
        var i = parseInt(document.getElementById("token-added").innerHTML);
        
        document.getElementById("token-added").innerHTML = i + 1;
        var numOfTags = i + 1;
        document.getElementById("shareSubmit").disabled = false;
        var tagsAdded = parseInt(document.getElementById("token-added").innerHTML);

        var allTokens = document.querySelectorAll(".tagify__tag");
        var lenAllTokens = allTokens.length;
        var lastelement = lenAllTokens < 1 ? "" : allTokens[lenAllTokens-1];
        if (tagsAdded > tagify.settings.maxTags){
            lastelement.classList.add("to-much");
        }
        if (numOfTags > tagify.settings.maxTags) {
          document.getElementById("shareSubmit").disabled = true;
          document.getElementById("errorMessageShare").innerHTML =
            JSi18n.conference_session_share_error_noMoreShares ||
            "<strong>No more shares left</strong>, only " +
              tagify.settings.maxTags +
              " are allowed for all conference videos!";
        }
        var invalidInputs = document.getElementsByClassName(
          "tagify--notAllowed"
        ).length;
        if (invalidInputs > 0) {
          document.getElementById("shareSubmit").disabled = true;
          document.getElementById("errorMessageShare").innerHTML =
            JSi18n.conference_session_share_error_removeInvalid ||
            "<strong>Remove invalid emails first!</strong>";
        }
      } else {
        document.getElementById("token-added").innerHTML = 0;
      }
    }

    function onInvalidTag(e) {
      document.getElementById("shareSubmit").disabled = true;
      var i = parseInt(document.getElementById("token-added").innerHTML);
      var numOfTags = i;
      if (numOfTags >= tagify.settings.maxTags) {
        document.getElementById("shareSubmit").disabled = true;
        document.getElementById("errorMessageShare").innerHTML =
          JSi18n.conference_session_share_error_noMoreShares ||
          "<strong>No more shares left</strong>, only " +
            tagify.settings.maxTags +
            " are allowed for all conference videos!";
      } else {
        document.getElementById("errorMessageShare").innerHTML =
          JSi18n.conference_session_share_error_removeInvalid ||
          "<strong>Remove invalid emails first!</strong>";
      }
    }

    function onRemoveTag(e) {
      if (tagify) {
        var validInputs = document.getElementsByClassName("tagify__tag");
        var clonedObj = [].slice.call(validInputs);
        var i = parseInt(document.getElementById("token-added").innerHTML);
        if (!e.detail.tag.classList.contains("tagify--notAllowed")) {
          i--;
        }
        document.getElementById("token-added").innerHTML = i;
        var invalidInputs = document.getElementsByClassName(
          "tagify--notAllowed"
        ).length;
        if (invalidInputs == 0 && i <= tagify.settings.maxTags) {
          document.getElementById("shareSubmit").disabled = false;
          document.getElementById("errorMessageShare").innerHTML = "";
        }
        
        var usedInputs = document.getElementsByClassName("used").length;
        var allInputs = document.getElementsByClassName("tagify__tag").length;
        if (usedInputs == allInputs) {
          document.getElementById("shareSubmit").disabled = true;
        }
        if (tagify.value.length == 0) {
          document.getElementById("shareSubmit").disabled = true;
        }
      } else {
        document.getElementById("token-added").innerHTML = 0;
      }
    }

    function parseJson() {
      if (tagify) {
        var validInputs = document.getElementsByClassName("tagify__tag");
        var clonedObj = [...Array.from(validInputs)];
        var newArray = [];
        for (var element of clonedObj) {
          if (!element.classList.contains("tagify--notAllowed")) {
            var stringElement = element.outerHTML;
            var start = stringElement.indexOf('value="') + 7;
            var subString = stringElement.substring(
              start,
              stringElement.length - 1
            );
            var end = subString.indexOf('"');
            stringElement = subString.substring(0, end);
            newArray.push(stringElement);
          }
        }
        var result = "";
        var useShareArray = usedShares.split(",");
        newArray.forEach(val => {
          result += val + ",";
        });
        return result.substr(0, result.length - 1);
      }
    }
  }
};
