const ua = window.navigator.userAgent
const isIE = /MSIE|Trident/.test(ua)

/**
 * [DOM] Changes a tab
 *
 * @param {HTMLElement} instance - the .tabs instance
 * @param {Number} index - the index
 */
const handleTabChange = (state, event) => {
  const { instance, id, index } = state

  if (!instance) return
  if (instance === 'profileTabs' && id === 'following') {
    setTimeout(() => {
      handleTabChange({
        instance: 'following',
        index: 0
      }, event)
    }, 100)
  }

  const Instance = document.getElementById(instance)
  if (!Instance) return

  const switcherEls = Instance.querySelectorAll('.tab__switcher > ul > li')
  if (!switcherEls || !switcherEls.length) return

  // Remove aria selected from switch items
  for (const tabSwitchEl of switcherEls) {
    if (!tabSwitchEl) continue
    tabSwitchEl.setAttribute('aria-selected', false)
    tabSwitchEl.setAttribute('aria-hidden', true)
  }
  // Remove active class on all children
  for (const child of Instance.children) {
    child.classList.remove('active')
  }
  // first would be for the switcher, to keep the structure minimal
  const tab = Instance.children.item(index + 1)
  tab.classList.add('active')

  const switcherItem = switcherEls.item(index)
  if (switcherItem) 
    switcherItem.setAttribute('aria-selected', true)
    switcherItem.setAttribute('aria-hidden', true)

  if (id)
    history.replaceState({ instance, id, index }, document.title, `#${id}`)

  if (event) {
    event.trigger('tabChanged', { instance: Instance, payload: { instanceId: instance, id, index }})
  }
}

export default {
  get tabsInstances () {
    return document.querySelectorAll('.tabs')
  },

  /**
   * Creates the HTML structure for a given .tabs > .tab element
   * @param {HTMLElement} instance
   * @param {object} options
   */
  makeTabs (instance, options, ctxEvent) {
    const tabs = instance.children

    if (!tabs.length) { return }

    const ids = Array.prototype.slice.call(tabs)
      .map(tab => tab.dataset.id)
      .filter(id => id !== undefined)

    const { heading } = instance.dataset

    if (!instance.id) {
      instance.id = Array.from(instance.classList).join('-')
    }

    const startupId = window.location.hash.replace('#', '')
    const startupIndex = startupId && ids.length > 0 ?
      ids.indexOf(startupId) :
      options && options.activeIndex ?
        options.activeIndex :
        0

    // Keeps the state of the active/clicked tab
    const state = {
      index: startupIndex,
      instance: instance.id,
      id: startupId
    }

    const swContainer = document.createElement('DIV')
    const switcher = document.createElement('UL')

    swContainer.classList.add('tab__switcher')
    switcher.classList.add('no-style')


    /**
     * If tabs have a data-id, switching them from hash is possible
     */
    if (ids.length > 0) {
      const eventName = isIE ? 'hashchange' : 'popstate'
      window.addEventListener(eventName, () => {
        const { hash } = window.location
        const id = hash ? hash.replace('#', '') : ids[0]
        const index = hash ? ids.indexOf(id) : 0
        Object.assign(state, { id, index })

        handleTabChange(state, ctxEvent)
      })
    } else {
      document.addEventListener('click', e => {
        if (!e.target) return
        const { target } = e
        const { nodeName, parentNode: { parentNode } } = target

        // Check if clicked on a tab switcher element
        if (!parentNode ||
          nodeName !== 'LI' ||
          !parentNode.classList.contains('tab__switcher') ||
          target.getAttribute('aria-selected') === 'true') {
          return
        }
        // all children of the switcher
        const children = [].slice.call(target.parentNode.children)
        const index = children.indexOf(target)
        const id = null

        Object.assign(state, { index, id })
        handleTabChange(state, ctxEvent)
      })
    }

    // Prepend the switcher items
    let i = 0
    for (let tab of tabs) {
      i += 1
      const { id, count } = tab.dataset
      const active = id && id === document.location.hash || state.index === i
      switcher.innerHTML += `
      <li aria-selected="${active}">
        ${id ? `<a href="#${id}" aria-conrols="${id}" role="tab">` : ``}${tab.getAttribute('data-title')}${count && Number.isInteger(Number(count)) ? ` <span class="tab__count">${count}</span>` : ''}${id ? `</a>` : ``}
      </li>
    `
    }

    if (heading) {
      const h = document.createElement('H3')
      h.textContent = heading
      h.classList.add('section__heading')
      h.classList.add('heading')
      swContainer.prepend(h)
    }
    swContainer.append(switcher)
    instance.prepend(swContainer)

    handleTabChange(state, ctxEvent)
    instance.classList.add('tabs--inited')
  },

  init (config, context) {
    // Queries the page for tabs
    const { tabsInstances } = this
    const { event } = context

    const { length } = tabsInstances
    if (!length) {
      return
    }

    // get each tab instance
    for (const instance of tabsInstances) {
      this.makeTabs(instance, config[instance.id || instance.dataset.id], event)
    }
  }
}
