import { slugify } from '../helpers/index'
const heading = 'Table of Contents'

export default {
  init (config, context) {
    const isArticle = document.querySelector('article[data-type="article"]')
    if (!isArticle) return
    const hasSpan = isArticle.querySelector('#with-toc')
    if (!hasSpan) return
    const { body } = document

    const tocBar = document.createElement('div')
    tocBar.id = 'toc'

    const tocContent = document.createElement('div')
    tocContent.id = 'tocContent'

    const h2s = isArticle.querySelectorAll('.article__data h2')
    const title = isArticle.querySelector('h1')

    for (let h2 of h2s) {
      const slugifyd = slugify(h2.textContent)
      h2.id = slugifyd
      const forToc = document.createElement('a')
      forToc.textContent = h2.textContent
      forToc.setAttribute('href', `#${slugifyd}`)
      tocContent.append(forToc)
    }

    const head = document.createElement('h3')
    head.textContent = heading
    tocContent.prepend(head)

    const smallTitle = document.createElement('h5')
    smallTitle.textContent = title.textContent

    const button = document.createElement('button')
    button.classList.add('button__unstyled')
    button.classList.add('arrow__right')
    button.classList.add('button__arrow')

    button.textContent = heading
    button.addEventListener('click', () => {
      body.classList.toggle('tocOpen')
    })

    tocBar.append(button)
    tocBar.append(smallTitle)
    body.append(tocContent)
    body.append(tocBar)

    document.addEventListener('click', e => {
      if (!body.classList.contains('tocOpen')) return

      if (tocContent.contains(e.target) || tocBar.contains(e.target)) return

      body.classList.remove('tocOpen')
    })

    setTimeout(() => {
      body.classList.add('tocShow')
    }, 1500)
  }
}
