import Siema from "siema";
// import Debug from 'debug'

export default {
  init() {
  var rvcSlider = document.getElementById('rvcSlider')
    if (!rvcSlider) return
        class CustomSiema extends Siema {
          hideArrows(prevArrowSelector, nextArrowSelector) {
            if (this.currentSlide === 0) {
              document.querySelector(prevArrowSelector).style.visibility =
                "hidden";
            } else {
              document.querySelector(prevArrowSelector).style.visibility =
                "visible";
            }
            const index = this.currentSlide;
            if (
              index === this.innerElements.length + 1 ||
              index + this.perPage >= this.innerElements.length
            ) {
              document.querySelector(nextArrowSelector).style.visibility =
                "hidden";
            } else {
              document.querySelector(nextArrowSelector).style.visibility =
                "visible";
            }
          }
        }

        let siemaOne = new CustomSiema({
          selector: ".rvcslider",
          onChange: function() {
            this.hideArrows(".prev", ".next");
          },
          onInit: function() {
            this.hideArrows(".prev", ".next");
          },
          duration: 600,
          easing: 'ease-out',
          perPage: {
            900: 5, 
            1000: 6,
            1240: 6
          },
          startIndex: 0,
          draggable: false,
          multipleDrag: false,
          threshold: 20,
          loop: false,
          rtl: false,
        });

        document
          .querySelector(".prev")
          .addEventListener("click", () => siemaOne.prev(5));
        document
          .querySelector(".next")
          .addEventListener("click", () => siemaOne.next(5));
  }
};
