import Siema from "siema";
// import Debug from 'debug'

export default {
  init() {
  var mustWatchSlider = document.getElementById('mustWatchSlider')
    if (!mustWatchSlider) return
        class mustWatchSliderSiema extends Siema {
          hideArrows(prevArrowSelector, nextArrowSelector) {
            if (this.currentSlide === 0) {
              document.querySelector(prevArrowSelector).style.visibility =
                "hidden";
            } else {
              document.querySelector(prevArrowSelector).style.visibility =
                "visible";
            }
            const index = this.currentSlide;
            if (
              index === this.innerElements.length + 1 ||
              index + this.perPage >= this.innerElements.length
            ) {
              document.querySelector(nextArrowSelector).style.visibility =
                "hidden";
            } else {
              document.querySelector(nextArrowSelector).style.visibility =
                "visible";
            }
          }
        }

        let siemaOneMustWatchSlider = new mustWatchSliderSiema({
          selector: ".mustWatchSlider",
          onChange: function() {
            this.hideArrows(".prevMustWatch", ".nextMustWatch");
          },
          onInit: function() {
            this.hideArrows(".prevMustWatch", ".nextMustWatch");
          },
          duration: 600,
          easing: 'ease-out',
          perPage: {
            900: 5, 
            1000: 5,
            1240: 5
          },
          startIndex: 0,
          draggable: false,
          multipleDrag: false,
          threshold: 20,
          loop: false,
          rtl: false,
        });

        document
          .querySelector(".prevMustWatch")
          .addEventListener("click", () => siemaOneMustWatchSlider.prev(5));
        document
          .querySelector(".nextMustWatch")
          .addEventListener("click", () => siemaOneMustWatchSlider.next(5));
  }
};
