import { createPopper } from '@popperjs/core'

export default {
  get vcr () {
    const rvc = document.querySelector('.rvc.box__border')
    return rvc && rvc.children && rvc.children.length > 0 ? rvc : false
  },

  get popperContentItem () {
    const { vcr } = this
    if (!vcr) return
    return vcr.parentNode.querySelector('.popper')
  },

  init (config) {
    const { popperContentItem, vcr } = this
    if (!popperContentItem) return

    createPopper(vcr, popperContentItem)
  }
}
