function insertAfter(newNode, referenceNode) {
  referenceNode.parentNode.insertBefore(newNode, referenceNode.nextSibling);
}

export default {
  init (conf, ctx) {
    const popularFeed = document.querySelector('section.popular')
    if (!popularFeed) return

    const fetchMore = popularFeed.querySelector('.fetch')
    if (!fetchMore) return

    const { event } = ctx

    const callbackRouter = (entries, observer) => {
      var entry = entries[0];
      var target = entry.target;

      if (entry.intersectionRatio > 0) {
        if (target.classList.contains('last')) {
          console.log('last in view')
          event.trigger('fetchMore')
        }
      }
    }

    let nextBatchEl

    event.on('fetchMore', () => {
      nextBatchEl = popularFeed.querySelector('.next__batch')
      console.log('fetching more content', mock)
      const clones = []

      // await API call

      // Mock:
      const children = nextBatchEl.children

      if (!clones.length) {
        for (let c of children) {
          if (!c.dataset.id || c.classList.contains('clone')) {
            continue
          }

          const clone = c.cloneNode(true)
          clones.push(clone)
        }
      }

      const referenceNode = nextBatchEl.querySelector('li.last')
      referenceNode.classList.remove('last')

      clones.forEach((clone, i) => {
        insertAfter(clone, referenceNode)
        clone.classList.add('clone')
        if (i === 0) {
          clone.classList.add('last')
        }
      })

      setTimeout(() => {
        obs()
      }, 500);
    })

    const obs = () => {
      const lastEl = document.querySelectorAll('.e__cards li.last')
      var observer = new IntersectionObserver(callbackRouter, { threshold: 0.3 });
      lastEl.forEach(observer.observe.bind(observer));
    }

    event.on('loaded', obs)
  }
}
