export const uppercase = string => string.charAt(0).toUpperCase() + string.slice(1)
export const focusOnFirstInput = form => {
  setTimeout(() => {
    const item = form.querySelector('input[type="text"]')
    if (item) item.focus()
  }, 150)
}
export function slugify (str) {
  return str ? str
    .toString()
    .toLowerCase()
    .replace(/[^\w ]+/g, '-')
    .replace(/ +/g, '-')
    : ''
}
