export default {
  init (config, { event }) {
    const page = document.querySelector('.privacyPage')
    if (!page) return

    const next = page.querySelector('.next')
    const prev = page.querySelector('.prev')

    const instance = page.querySelector('.tabs')
    const tabs = instance.querySelectorAll('.tab')
    const ids = Array.prototype.slice.call(tabs).map(tab => tab.dataset.id)
    const bd = 'button__disabled'

    event.on('tabChanged', e => {
      const { index } = e.payload
      if (ids[index - 1]) {
        prev.classList.remove(bd)
        prev.setAttribute('href', `#${ ids[index - 1] }`)
      } else {
        prev.classList.add(bd)
        prev.removeAttribute('href')
      }
      if (ids[index + 1]) {
        next.classList.remove(bd)
        next.setAttribute('href', `#${ ids[index + 1] }`)
      } else {
        next.classList.add(bd)
        next.removeAttribute('href')
      }

      window.scrollTo(0, 0)
    })

    if (!window.location.hash)
      event.trigger('tabChanged', { payload: { index: 0 } })
  }
}
